import { Component, Input } from '@angular/core';
import { FORM_BUILDER } from '../../form-builder.constant';
import { FormSectionModel } from '../../models/form-section.model';
import { FormControlModel } from '../../models/form-control.model';
import { FormWidgetModel } from '../../models/form-widget.model';

@Component({
  selector: 'rx-form-section',
  templateUrl: './form-section.component.html'
})
export class FormSectionComponent {
  @Input() guid: string;
  @Input() section: FormSectionModel;

  controlTypes = FORM_BUILDER.controlTypes;

  constructor() {
    this.trackBySectionControls = this.trackBySectionControls.bind(this);
  }

  trackBySectionControls(index: number, item: FormControlModel | FormWidgetModel): string {
    return this.guid + (item as FormControlModel).name || `${item.component.name}` || String(index);
  }
}
